import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ContentAuthService } from '../../services/content-auth.service';
import { LoginConfig, UnifiedLoginComponent } from '../../../shared/components/unified-login/unified-login.component';

@Component({
  selector: 'app-content-login',
  template: '<app-unified-login #loginComponent [config]="loginConfig" (loginSubmit)="onLogin($event)"></app-unified-login>',
  standalone: false
})
export class ContentLoginComponent implements OnInit {
  @ViewChild('loginComponent') loginComponent?: UnifiedLoginComponent;

  loginConfig: LoginConfig = {
    title: 'üé¨ ÏΩòÌÖêÏ∏†',
    subtitle: 'ÌÅ¨Î¶¨ÏóêÏù¥ÌÑ∞ÏôÄ Ìï®ÍªòÌïòÎäî ÏòÅÏÉÅ ÌîåÎû´Ìèº',
    moduleName: 'content',
    showSocialLogin: false,
    showRegisterLink: false
  };

  returnUrl: string = '/content';

  constructor(
    private authService: ContentAuthService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    if (this.authService.isLoggedIn()) {
      this.router.navigate([this.returnUrl]);
      return;
    }

    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/content';
  }

  async onLogin(credentials: { username: string; password: string }): Promise<void> {
    try {
      await this.authService.mockLogin(credentials.username, credentials.password);
      this.router.navigate([this.returnUrl]);
    } catch (error: any) {
      if (this.loginComponent) {
        this.loginComponent.setError(error.message || 'Î°úÍ∑∏Ïù∏ Ïã§Ìå®');
      }
    }
  }
}
