import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';

export interface ContentUser {
  username: string;
  nickname: string;
  avatar: string;
  level: number;
  subscribers: number;
  uploadedVideos: number;
}

@Injectable({
  providedIn: 'root'
})
export class ContentAuthService {
  private currentUserSubject: BehaviorSubject<ContentUser | null>;
  public currentUser: Observable<ContentUser | null>;

  private readonly STORAGE_KEY = 'content_user';
  private readonly TOKEN_KEY = 'content_token';

  constructor(private router: Router) {
    const storedUser = localStorage.getItem(this.STORAGE_KEY);
    this.currentUserSubject = new BehaviorSubject<ContentUser | null>(
      storedUser ? JSON.parse(storedUser) : null
    );
    this.currentUser = this.currentUserSubject.asObservable();
  }

  public get currentUserValue(): ContentUser | null {
    return this.currentUserSubject.value;
  }

  mockLogin(username: string, password: string): Promise<any> {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const mockUsers: any = {
          'admin': {
            username: 'admin',
            nickname: 'ContentAdmin',
            avatar: 'üé¨',
            level: 99,
            subscribers: 50000,
            uploadedVideos: 250
          },
          'user1': {
            username: 'user1',
            nickname: 'Creator1',
            avatar: 'üìπ',
            level: 25,
            subscribers: 5000,
            uploadedVideos: 45
          }
        };

        if (mockUsers[username] && password === '1017') {
          const user = mockUsers[username];
          const token = 'mock_content_token_' + username;
          
          localStorage.setItem(this.STORAGE_KEY, JSON.stringify(user));
          localStorage.setItem(this.TOKEN_KEY, token);
          
          this.currentUserSubject.next(user);
          resolve({ user, token });
        } else {
          reject({ message: 'ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§' });
        }
      }, 500);
    });
  }

  logout(): void {
    console.log('[ContentAuth] Î°úÍ∑∏ÏïÑÏõÉ');
    
    try {
      localStorage.removeItem(this.STORAGE_KEY);
      localStorage.removeItem(this.TOKEN_KEY);
      this.currentUserSubject.next(null);
    } catch (error) {
      console.error('[ContentAuth] Î°úÍ∑∏ÏïÑÏõÉ Ï§ë Ïò§Î•ò:', error);
    } finally {
      // Ïò§Î•ò Î∞úÏÉù Ïó¨Î∂ÄÏôÄ Í¥ÄÍ≥ÑÏóÜÏù¥ Ìï≠ÏÉÅ ÏΩòÌÖêÏ∏† Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.router.navigate(['/content/login']).catch((err: any) => {
        console.error('[ContentAuth] ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏòàÏô∏ Î∞úÏÉù:', err);
        window.location.hash = '#/content/login';
      });
    }
  }

  isLoggedIn(): boolean {
    return !!this.currentUserValue && !!localStorage.getItem(this.TOKEN_KEY);
  }

  getToken(): string | null {
    return localStorage.getItem(this.TOKEN_KEY);
  }
}
