import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ContentMainComponent } from './components/content-main/content-main.component';
import { ContentLoginComponent } from './components/content-login/content-login.component';
import { ContentAuthGuard } from './guards/content-auth.guard';

const routes: Routes = [
  {
    path: 'login',
    component: ContentLoginComponent
  },
  {
    path: '',
    component: ContentMainComponent,
    canActivate: [ContentAuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ContentRoutingModule { }
