import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { ContentAuthService } from '../services/content-auth.service';

@Injectable({
  providedIn: 'root'
})
export class ContentAuthGuard implements CanActivate {
  constructor(
    private contentAuthService: ContentAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (this.contentAuthService.isLoggedIn()) {
      return true;
    }

    return this.router.createUrlTree(['/content/login'], {
      queryParams: { returnUrl: state.url }
    });
  }
}
